<template>
	<div class="content">
    <div class="breadcrumbs">
      <div class="bread-menus active">
        <span class="numb">1</span> Delivery
      </div>
      <div class="bread-menus">
        <span class="numb">2</span> Delivery
      </div>
      <div class="bread-menus">
        <span class="numb">3</span> Delivery
      </div>
    </div>
    <div class="top-left-navs">
      <a href="#"><i class="fas fa-arrow-left"></i> Back to chart</a>
    </div>
    <div class="row-custom ">
      <div class="col-8">
        <div class="d-flex justify-content-between">
          <div class="title">Delivery details</div>
          <div class="right-option"> 
            <label for="dropshipper">
              <input type="checkbox" name="" id="dropshipper"> Send as dropshipper
            </label>
          </div>
        </div>
        <div class="row-custom forms">
          <div class="col-7">
            <input type="text" name="" id="" class="custom-field" placeholder="Name">
            <input type="text" name="" id="" class="custom-field" placeholder="Phone number">
            <textarea name="" id="" cols="30" rows="5" class="custom-field"></textarea>
          </div>
          <div class="col-5">
              <input type="text" name="" id="" class="custom-field" placeholder="Dropshipper name">
              <input type="text" name="" id="" class="custom-field" placeholder="Dropshipper phone number">

          </div>
        </div>
      </div>
      <div class="col-4">
        <div class="right-panel">
          <div class="top-section">
            <div class="sec-title">Summary</div>
            <p class="sub-title">10 item purchased</p>
          </div>
          <div class="calculate">
            <table class="tables no-border">
              <tr>
                <td class="item-names">Cost of goods</td>
                <td>Rp. 500, 000,-</td>
              </tr>
              <tr>
                <td class="item-names">Drop shipping fee</td>
                <td>Rp. 500, 000,-</td>
              </tr>
              <tr>
                <td class="sec-title text-left">Total</td>
                <td class="sec-title">Rp. 1000,000,-</td>
              </tr>
            </table>
            <button class="btn-custom btn-orange">Continue to Payment</button>
          </div>
        </div>

      </div>
    </div>
  </div>
	<div>
		<input v-on:click="setDropFee"  type="checkbox"> Send as Dropshipper

		<input v-on:keyup="requiredHandle" type="text" name="name" placeholder="Name" v-bind:class="{'error':isError.name, 'success':isSuccess.name}">
		<input v-on:keyup="emailHandle" type="text" name="email" placeholder="Email" v-bind:class="{'error':isError.email, 'success':isSuccess.email}">
		<input v-on:keyup="phoneHandle" type="text" name="phone" placeholder="Phone Number" v-bind:class="{'error':isError.phone, 'success':isSuccess.phone}">
		<input v-on:keyup="addressHandle" v-bind:class="{'error':isError.address, 'success':isSuccess.address}" name="address" type="text" placeholder="Delivery Address">
		<span>{{ countSisaAddress }} Characters Available</span>

		<br>
		<input v-on:keyup="requiredHandle" v-model="dropName" v-bind:class="{'error':isError.dropName, 'success':isSuccess.dropName}" name="dropName" type="text" placeholder="Dropshipper Name" :disabled="!sendDropCheckBox">
		<input v-on:keyup="phoneHandle" v-model="dropPhoneNum" v-bind:class="{'error':isError.dropPhoneNum, 'success':isSuccess.dropPhoneNum}" name="dropPhoneNum" type="text" placeholder="Dropshipper Phone Number" :disabled="!sendDropCheckBox">
		<hr>

		<span>Cost of Good</span>
		<span id="drop-good">{{ cosOfGood }}</span>
		<br>

		<span>Dropsipper fee</span>
		<span  id="drop-fee">{{ dropFee }}</span>
		<br>

		<span>Total</span>
		<span  id="total">{{ countTotal }}</span>
		<button :disabled="getTotalError">Continue to Payment</button>
	</div>
</template>

<script>
export default {
  name: 'Step1',
  data () {
    return {
      sendDropCheckBox: false,
      name : '',
      phone : '',
      address : '',
      dropName : '',
      dropPhoneNum : '',
      cosOfGood : 50000,
      dropFee: 0,
      total: 0,
      countSisaAddress: 0,
      initForm: true,
      isError: {
      	name: false,
      	phone: false,
      	dropName: false,
      	dropPhoneNum: false,
      	address: false,
      	email: false,
      },
      isSuccess: {
      	name: false,
      	phone: false,
      	dropName: false,
      	dropPhoneNum: false,
      	address:false,
      	email:false
      }
    }
  },
  methods: {
  	setDropFee: function () {
	  	this.sendDropCheckBox = !this.sendDropCheckBox;
  		if (this.sendDropCheckBox) {
  			this.dropFee = 5900;
  		} else {
	  		this.dropFee = 0;
	  		//clear disabled form and border
	  		this.isError.dropName = false;
  			this.isSuccess.dropName = false;
  			this.isError.dropPhoneNum = false;
  			this.isSuccess.dropPhoneNum = false;
			this.dropName = '';
			this.dropPhoneNum = '';
	  	}
  	},
  	requiredHandle: function (e) {
  		this.initForm = false;
  		var val = e.target.value;
  		var name = e.target.name;
  		this[name] = val;
  		if (val.length > 0) {
  			this.isError[name] = false;
  			this.isSuccess[name] = true;
  		} else {
  			this.isError[name] = true;
  			this.isSuccess[name] = false;
  		}
  	},
  	phoneHandle: function (e) {
  		this.initForm = false;
  		var regex = /^[+]?[\s./0-9]*[(]?[0-9]{1,4}[)]?[-\s./0-9]*$/g;
  		var val = e.target.value;
  		var name = e.target.name;
  		this[name] = val;
  		if (val.length >= 6 && val.length <= 20) {
  			if (val.match(regex)) {
  				this.isError[name] = false;
	  			this.isSuccess[name] = true;
  			} else {
  				this.isError[name] = true;
	  			this.isSuccess[name] = false;
  			}
  		} else {
  			this.isError[name] = true;
  			this.isSuccess[name] = false;
  		}
  	},
  	addressHandle: function (e) {
  		this.initForm = false;
  		var val = e.target.value;
  		var name = e.target.name;
  		this[name] = val;
  		this.countSisaAddress = 120 - val.length;
  		if (this.countSisaAddress < 0) this.countSisaAddress = 0;
  		if (val.length > 0 && val.length <= 120) {
  			console.log('123');
  			console.log(name);
  			this.isError[name] = false;
  			this.isSuccess[name] = true;
  		} else {
  			this.isError[name] = true;
  			this.isSuccess[name] = false;
  		}
  	},
  	emailHandle: function (e) {
  		this.initForm = false;
  		var regex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  		var val = e.target.value;
  		var name = e.target.name;
  		this[name] = val;
  		console.log(regex.test(val));
		if (val.match(regex)) {
			this.isError[name] = false;
  			this.isSuccess[name] = true;
  		} else {
  			this.isError[name] = true;
  			this.isSuccess[name] = false;
  		}
  	},
  },
  computed : {
  	countTotal: function () {
  		return this.dropFee + this.cosOfGood;
    },
    getTotalError: function() {
    	// if (this.initForm) return true;
    	for (var key in this.isError) {
		    if (this.isError.hasOwnProperty(key) && (this.isError[key] || this[key] == '')) {
		    	// console.log(this.isError.hasOwnProperty(key));
		        return true;
		    }
		}
		return false;
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.error {
	border: 1px solid red;
}

.success {
	border: 1px solid green;
}
</style>



